#SKILLS {
  background: -webkit-gradient(linear, left top, left bottom, from(#000), color-stop(#0b1419), to(#172832));
  background: -webkit-linear-gradient(#000, #0b1419, #172832);
  background: -o-linear-gradient(#000, #0b1419, #172832);
  background: linear-gradient(#000, #0b1419, #172832);
  text-align: center;
  width: 100vw;
  height: 90vh;
}

#SKILLS h1 {
  color: #fff;
  font-size: 4rem;
  text-align: center;
  text-shadow: 0 0 5px #cac1c1;
  margin-top: 10%;
  display: inline-block;
  padding: 10px 3px 40px;
  /* bottom padding should be higher to make up for pseudo border height */
  background: -webkit-gradient(
    linear,
    left top, right top,
    color-stop(25%, transparent),
    color-stop(25%, #cac1c1),
    color-stop(75%, #cac1c1),
    color-stop(75%, transparent)
  );
  background: -webkit-linear-gradient(
    left,
    transparent 25%,
    #cac1c1 25%,
    #cac1c1 75%,
    transparent 75%
  );
  background: -o-linear-gradient(
    left,
    transparent 25%,
    #cac1c1 25%,
    #cac1c1 75%,
    transparent 75%
  );
  background: linear-gradient(
    90deg,
    transparent 25%,
    #cac1c1 25%,
    #cac1c1 75%,
    transparent 75%
  );
  background-size: 100% 2px;
  background-position: 0 100%;
  background-repeat: no-repeat;
}
.img img {
  width: 180px;
  height: 180px;
  padding: 6%;
}
.detail {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 10%;
}
.img .text_overlay {
  width: 185px;
  height: 185px;
  -ms-flex-align: center;
      align-items: center;
  position: absolute;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  text-align: center;
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
  border-radius: 50%;
  overflow: hidden;
  opacity: 0;
  margin-top: 0.8%;
  margin-left: 0.7%;
}
.detail .img .text_overlay:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  letter-spacing: 2px;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 1rem;
  opacity: 1;
  z-index: 1;
}
/* Responsive Styling : PC */
@media only screen and (max-width: 992px) {
  .img img {
    width: 150px;
    height: 150px;
  }
  .img .text_overlay {
    position: absolute;
    width: 153px;
    height: 153px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin-top: 0.7%;
    margin-left: 0.7%;
  }
}
/* Responsive Styling : Tablet */
@media only screen and (max-width: 820px) {
  .img img {
    width: 100px;
    height: 100px;
    padding: 5%;
  }
  .img .text_overlay {
    position: absolute;
    width: 105px;
    height: 105px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin-top: 0.5%;
    margin-left: 0.4%;
  }
}
/* Responsive Styling : Mobile */
@media only screen and (max-width: 425px) {
  #SKILLS h1 {
    font-size: 3.5rem;
    margin-top: 30%;
  }
  .detail {
    padding: 5%;
  }
  .img img {
    width: 70px;
    height: 70px;
    padding: 5%;
  }
  .img .text_overlay {
    width: 75px;
    height: 75px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin-top: 0.5%;
    margin-left: 0.8%;
  }
}



